import {createDomain, Effect} from 'effector';

import {
  ClickOnStoryInternalWithDataPayload,
  FontResource,
  STORY_LIST_TYPE,
  STORY_READER_WINDOW_REFERER
} from "../story-manager/common.h";
import {Dict} from "../../global.h";

// use name|sid or  "effector/babel-plugin"
const toServerFxDomain = createDomain("serverFxDomainWidgetStory");
const toClientFxDomain = createDomain("clientFxDomainWidgetStory");

export {toServerFxDomain as serverFxDomain, toClientFxDomain as clientFxDomain}

export const rpcDomains = {
  serverFxDomain: toServerFxDomain,
  clientFxDomain: toClientFxDomain
}

// export const workerMessage = domain.effect<string, string[]>();
// export const failure = domain.effect<void, any>();
// export const ping: Effect<{now: number}, {ping: number}> = domain.effect();



export const RpcMessage = toClientFxDomain.effect<string, string[]>("RpcMessage");

export const setStories = toClientFxDomain.effect<{listType: STORY_LIST_TYPE, storyList: Array<Dict>}, void>("setStories");
export const setFonts = toClientFxDomain.effect<FontResource, void>("setFonts");




export const onOpenReader = toClientFxDomain.effect<ClickOnStoryInternalWithDataPayload & {hasShare: boolean}, void>("onOpenReader");
export const onCloseReader = toClientFxDomain.effect<void, void>("onCloseReader");


export const RpcMessageOnServer = toServerFxDomain.effect<string, string[]>("RpcMessageOnServer");

export const widgetLoaded = toServerFxDomain.effect<void, void>("widgetLoaded");


export const onReaderOpened = toServerFxDomain.effect<{windowReferer: STORY_READER_WINDOW_REFERER}, void>("onReaderOpened");
export const onReaderClosed = toServerFxDomain.effect<{windowReferer: STORY_READER_WINDOW_REFERER}, void>("onReaderClosed");

export const widgetEvent = toServerFxDomain.effect<{name: string, payload: Dict, cb?: {id: string}}, void>("widgetEvent");
export const widgetEventCb = toClientFxDomain.effect<{requestId: string, success: boolean, data: Dict, err: any}, void>("widgetEventCb");

export const onShareStoryInternal = toServerFxDomain.effect<{title?: string, text?: string, url?: string, files?: Array<string>}, void>("shareStoryInternal");
